<template>
  <div>
    <fu-search-bar quick-placeholder=" / " :components="components" @exec="search">
      <fu-search-bar-button icon="el-icon-more" tooltip=""/>
      <fu-search-bar-button icon="el-icon-download" tooltip="Excel"/>
    </fu-search-bar>
  </div>
</template>

<script>
import FuSearchBar from "@/components/search-bar"
import FuSearchBarButton from "@/components/search-bar/FuSearchBarButton";

const getOptions = () => {
  const options = [
    {label: "", value: "Success"},
    {label: "", value: "Fail"}
  ]
  return new Promise((resolve, reject) => {
    setTimeout(() => {
      resolve(options)
    }, 3000)
  })
}

export default {
  name: "SearchDemo",
  components: {FuSearchBarButton, FuSearchBar},
  data() {
    return {
      components: [
        {field: "name", label: "", component: "FuComplexInput", defaultOperator: "eq"},
        {field: "email", label: "Email", component: "FuComplexInput"},
        {
          field: "status",
          label: "",
          component: "FuComplexSelect",
          options: [
            {label: "", value: "Running"},
            {label: "", value: "Success"},
            {label: "", value: "Fail"}
          ],
          multiple: true,
          filterable: true,
        },
        {
          field: "async_status",
          label: "",
          component: "FuComplexAsyncSelect",
          initOptions: (options, loading) => {
            getOptions().then(response => {
              response.forEach(o => {
                options.push(o)
                loading.status = false
              })
            })
          },
        },
        {field: "create_time", label: "", component: "FuComplexDateTime"},
      ]
    }
  },
  methods: {
    search(condition, e) {
      console.log(condition, e)
    }
  }
}
</script>

