const type =[
    {
        name:'',
        value:'components'
    },
    {
        name:'',
        value:'filters'
    },
    {
        name:'',
        value:'directives'
    },

]
const navList = {
    "components": [
        {
            name: 'ComplexTable ',
            path: 'table',
            component: 'ComplexTableDemo'
        },
        {
            name: 'Speed Dial',
            path: 'speed-dial',
            component: 'SpeedDialDemo'
        },
        {
            name: 'SearchBar ',
            path: 'search',
            component: 'SearchDemo'
        },

    ],
    "filters": [],
    "directives": [],
}
function getTypeName(val) {
    let name
    type.map(item => {
        if (val === item.value) { name = item.name }
    })
    return name || ''
}
function getComponent(path,type) {
    let componentName
    console.log(type)
    navList[type].map(item => {
        if (path === item.path) { componentName = item.component }
    })
    return componentName || 'table'
}
// export default navList
export {
    navList,
    getComponent,
    getTypeName
}